@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
box-sizing: inherit;
}

* {
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: lightgrey;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0;
  overflow: auto;
}

#root {
  margin: 3em 0;
}
.content-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.toggle-preview {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-bottom: 1em;
}

.toggle-input {
  opacity: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: grey;
  transition: 0.4s;
}

.toggle-slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

.toggle-input:checked + .toggle-slider {
  background-color: rgb(70, 70, 70);
}

.toggle-input:focus + .toggle-slider {
  box-shadow: 0 0 1px white;
}

.toggle-input:checked + .toggle-slider:before {
  -webkit-transform: translateX(26px);
          transform: translateX(26px);
}

.toggle-slider {
  border-radius: 34px;
}

.resume-page {
  display: -webkit-flex;
  display: flex;
  height: 700px;
  min-width: 70vw;
  border: 1px solid black;
  box-shadow: -10px 10px 5px rgba(0, 0, 0, 0.5);
}

.side-bar {
  background-color: rgb(36, 72, 170);
  min-width: 35%;
  color: white;
  padding: 2em 1.5em;
  overflow: auto;
}

.side-name {
  margin: 0;
}

.side-position {
  margin-top: 0.3em;
}

.side-heading {
  margin-top: 1em;
  margin-bottom: 0.5em;
}

main {
  overflow: auto;
  min-width: 65%;
  padding: 2em 1em;
}

.summary-heading {
  margin-top: 0;
  margin-bottom: 0.5em;
}

.experience-heading {
  margin-top: 1.5em;
  margin-bottom: 0.5em;
}
.skill-list {
  padding: 0;
  list-style: none;
}

.experience-list {
  padding-left: 1em;
}

.skill-edit li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.skill-edit button {
  border: none;
  background-color: transparent;
  font-size: 0.9em;
  color: white;
  padding: 0;
}

.skill-edit button:hover {
  cursor: pointer;
  color: grey;
}
.experience {
  display: -webkit-flex;
  display: flex;
}

.dates {
  margin-top: 0.4em;
  margin-right: 2em;
  max-width: 6em;
}

.experience-label {
  margin-bottom: 0;
}

.school-name {
  margin: 0;
}

.experience-institution {
  margin: 0;
}

.experience-location {
  margin-top: 0;
}

.experience-edit {
  font-size: 0.9em;
}

.experience-edit .dates {
  margin-right: 0;
}

.experience-edit .experience {
  border-left: 2px solid grey;
  padding-left: 0.8em;
  margin: 0.8em 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.experience-btn-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-right: 0.5em;
}

.experience-btn-wrapper button {
  border: none;
  background-color: transparent;
  font-size: 1.2em;
}

.experience-btn-wrapper button:hover {
  cursor: pointer;
  color: grey;
}

.preview-contact {
  margin: 3em 0;
}
.input-field {
  margin: 0.5em 0;
  border-radius: 2px;
  border: none;
  height: 2.5em;
  padding: 0 1em;
}

input[type='date']:invalid {
  color: grey;
}
textarea {
  max-width: 100%;
  margin: 0.5em 0;
  border: none;
  border-radius: 2px;
  min-height: 7em;
  padding: 0.7em 1em;
}
.input-wrapper {
  margin: 1em 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

label {
  font-weight: bold;
  font-size: 0.85em;
}
button[type='reset'] {
  margin-right: 1em;
}

button[type='submit'],
button[type='reset'] {
  padding: 0.5em 1em;
  background-color: grey;
  border: none;
  border-radius: 5px;
  font-size: 0.9em;
  color: white;
}

button[type='submit']:hover,
button[type='reset']:hover {
  background-color: rgb(218, 218, 218);
  color: black;
  box-shadow: 0 0 0 1px black;
  cursor: pointer;
}
